@import url('https://fonts.googleapis.com/css2?family=Acme&family=Great+Vibes&family=Kalam:wght@300;400;700&display=swap');


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  /* overflow-x: hidden; */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

section{
  height: 100vh;
}

.landing{
  background:url('./Components/Resources/5471-removebg-preview.png');
  background-color: #e4c2c1;
  background-repeat: no-repeat;
  background-size: 550px;
  background-position:  top center;
}

.header{
  /* position:fixed; */
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 100px;
  /* background: #d1a080; brownish */
  /* background-color: #b6666f; */
  background: rgba(255, 255, 255, .1);
  display: flex;
  justify-content: space-between;
  backdrop-filter: 10px;
  border-bottom: rgba(255, 255, 255, .2) ;
}

.logo{
  color: black;
  text-decoration: none;
  font-family: 'Kalam', cursive;;
  font-size: 30px;
  font-weight: 900;
}

.navbar a{
  color:black;
  text-decoration: none;
  font-size: 22px;
  font-weight: 600;
  margin-right: 35px;
  transition: 0.3s;
}

.navbar a:hover{
  color: #f53163;
  transform: scale(1.2);
}

.curve img{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

