.body{
  display: flex;
  flex-direction: column;
  height:89vh;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.curve{
  width: 100%;
  align-self: baseline;
  margin-top: auto;
}

.headingText{
  align-self: center;
  justify-self: center;
  margin-top: auto;
  display: flex;
  flex-direction: column;
}

.headingText .text{
  max-width: 700px;
  position: relative;
  color: #f53163;
  font-size: 60px;
  font-weight: 900;
}

.headingText .text.first-text{
  color: white;
}

.text.sec-text::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #e4c2c1;
  border-left: 4px solid #f53163 ;
  animation: animate 4s steps(12) infinite;
}

@keyframes animate {
  40%, 60%{
    left:calc(100% + 10px) ;
  }
  100%{
    left: 0%;
  }
}

.login{
  /* z-index: 10; */
  overflow-x: hidden;
  background-color: #b6666f;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  margin-top: auto;
}

.login img{
  transform: translateY('47px');
}

.contain{
  display: flex;
  align-items: flex-end;
}

.wrapper{
  position: relative;
  width: 400px;
  height: 440px;
  background: #fef2f2;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  backdrop-filter: 20px;
  box-shadow: 0 0 30px rgba(0, 0, 0, .5);
  display: flex;
  justify-content: center;
  align-items: center;
  /* z-index: 20; */
}

.form-box{
  width: 100%;
  padding: auto;
  height: 100%;
  transition:opacity 0.5s;
}

.form{
  /* padding: auto; */
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 70%;
}

.registration .form{
  height: 80%;
}

.form-box h2{
  /* font-size: 25px !important; */
  color: black;
  text-align: center;
  padding-top: 30px;
}

.input-box{
  position: relative;
  width: 80%;
  height: 50px;
  align-self: center;
  border-bottom: 2px solid black;
  margin: auto ;
}

.input-box label{
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-45%);
  color: #282c34;
  font-weight: 500;
  pointer-events: none;
  transition: 0.5s;
}

.input-box input:focus~label,
.input-box input:valid~label{
  top: -5px;
}

.input-box input{
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  color: black;
}

.icon{
  position: absolute;
  right:2%;
  line-height: 55px;
}

.remember{
  /* font-size: 0.9rem; */
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 1rem auto;
}

.remember label input{
  accent-color: black;
  margin-right: 5px;
}

.remember a{
  color: #282c34;
  text-decoration: none;
  transition: 0.3s;
}

.remember-register{
  margin-top:auto;
  margin-bottom:auto;
  justify-content: center;
  accent-color: black;
  margin-right: 5px;
}

.remember a:hover{
  color: rgb(158, 28, 97);
  text-decoration: underline;
}

.btn{
  width: 80%;
  background-color: #282c34;
  color: whitesmoke;
  border: none;
  outline: none;
  font-size: 1rem;
  font-weight: 400;
  transition: 0.3s;
}

.btn:hover{
  transform: scale(1.1);
  background-color: #282c34;
  color: #b6666f;
}

.register{
  color: #282c34;
  text-align: center;
  margin: auto;
}

.register p span{
  margin-left: 5px;
  color: black;
  text-decoration: none;
  font-weight: 600;
  transition: 0.3s;
}

.register p span:hover{
  color:rgb(158, 28, 97) ;
  text-decoration: underline;
}

.form-box.sign{
  display:none;
}

.search{
  /* z-index: 20; */
  overflow-x: hidden;
  background-color: #d1a080;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
  margin-top: auto;
}

.card-deck{
  display: flex;
}

.card{
  display: flex;
  justify-content: center;
  align-items: center;
}

.projects_container .col{
  display: none;
}

.projects_container .active_work{
  transition: 1s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.line-1{
  z-index: 35;
  opacity: 0.5;
}
.line-2{
  z-index: 30;
  opacity: 0.7;
}
.line-3{
  z-index: 25;
  /* opacity: 0.5; */
}

.line {
  position: relative;
  width: 100%;
  height: 50px;
  overflow: hidden;
}

.wave {
  position: absolute;
  bottom: 0;
  width: 200%;
  height: 100%;
  background-repeat: repeat no-repeat;
  background-position: 0 bottom;
  transform-origin: center bottom;
  animation: waveAnimation 2s linear infinite;
}

@keyframes waveAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
